@import url(https://fonts.googleapis.com/css?family=PT+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: PT Sans, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.Tooltip {
  position: absolute;
  z-index: 1;

}

.Tooltip__container {
  position: absolute;
  width: 150px;
  padding: 1rem;
  pointer-events: none;
  background: white;
  box-shadow: 3px 3px 5px 0px rgba(176,169,176,1);
}

.Tooltip__line {
  pointer-events: none;
  position: absolute;


  top: 0;
  bottom: 0;
  width: 1px;

  background: rgba(0, 0, 0, 0.28);
}

.Legend {
    position: absolute;
    top: 80px;
    right: 200px;
}

.Diagram{
    position: relative;
}


